.payment-form {
  font-family: 'Montserrat', sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 100%;
  max-width: 400px;
}
.payment-form .payment-info {
  padding: 20px 20px;
}
.payment-form .title {
  font-weight: 400;
  font-size: 20px;
}
.payment-form .product-name {
  margin: 6px 0;
}
.payment-form .product-price {
  font-weight: 600;
  font-size: 20px;
}
.payment-form .form-block {
  padding: 20px;
}
.payment-form .form-block .form-input {
  width: 100%;
  margin-bottom: 10px;
}
.payment-form .form-block .form-footer {
  padding-top: 10px;
  text-align: right;
}
.payment-form .phone-confirmation-text {
  margin-bottom: 15px;
}
.payment-form .success-text {
  margin-top: 25px;
  margin-bottom: 15px;
  font-weight: 500;
}
.payment-form .hidden {
  display: none;
}
.payment-form .success-icon-container {
  text-align: center;
}
.payment-form .success-icon-container .success-icon {
  width: 150px;
  height: 150px;
  color: #0d8f0d;
}
.payment-form .error {
  color: red;
  text-align: center;
  padding: 20px;
}
